import Vue from 'vue'
import App from './App.vue'
import router, { admin } from './router'

import store from './store'
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import '@/assets/media/pc.scss'
import GlobalFilter from '@/utils/filter/globalFilter'
import GlobalMixIn from '@/utils/mixins/globalMixIn'
import RegisterComponents from '@/utils/registerComponents'
import Directive from '@/utils/directives'
Vue.config.productionTip = false


//自定义指令
Vue.use(Directive)

//编辑器
Vue.use(mavonEditor)

// Filter
Vue.use(GlobalFilter)

//全局mixin 
Vue.use(GlobalMixIn)

//注册所有组件
Vue.use(RegisterComponents)





// 守卫
router.beforeEach(async (to, from, next) => {
  // 退出登录 删除token 删除管理员路由
  if (to.path === "/login" && from.path !== '/' && from.path !== '/register') {
    store.dispatch('logout')
  }

  if (to.path !== '/login' && to.path !== '/register') {
    // 初始获取用户信息
    if (!store.state.user.isLogin) {
      store.dispatch("getUser");
    }
    // 添加路由管理员
    if (store.state.user.user.identity === 'root' && !store.state.header.addAdminRoute) {
      await store.commit('ADDADMINROUTE')
      router.addRoutes(admin)
    }
  }
  next()
})


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')


